import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';

import { environment } from "@env/environment";

@Injectable({
  providedIn: 'root'
})
export class HomeService {

  private baseUrl = environment.apiUrl;

  constructor(private http: HttpClient) { }

  getPaymentList(searchData: String, page: Number, limt: Number, sortColumn: String, SortDirection: String) {
    let url: any;
    if (searchData) url = this.baseUrl + "/users/admins/home/payment-info" + "?search=" + searchData;
    else
      url =
        this.baseUrl +
        "/users/admins/home/payment-info" + "?page=" +
        page +
        "&limit=" +
        limt +
        "&sortColumn=" +
        sortColumn +
        "&sortDirection=" +
        SortDirection;

    return this.http.get<any>(url).pipe(map((obj) => obj || []));
  }

  getAssessmentsList(searchData: String, page: Number, limt: Number, sortColumn: String, SortDirection: String) {
    let url: any;
    if (searchData) url = this.baseUrl + "/users/teachers/home/assements" + "?search=" + searchData;
    else
      url =
        this.baseUrl +
        "/users/teachers/home/assements" + "?page=" +
        page +
        "&limit=" +
        limt +
        "&sortColumn=" +
        sortColumn +
        "&sortDirection=" +
        SortDirection;

    return this.http.get<any>(url).pipe(map((obj) => obj || []));
  }

  getAssessmentScoreList(searchData: String, page: Number, limt: Number, sortColumn: String, SortDirection: String) {
    let url: any;
    if (searchData) url = this.baseUrl + "/users/teachers/home/assement-scores" + "?search=" + searchData;
    else
      url =
        this.baseUrl +
        "/users/teachers/home/assement-scores" + "?page=" +
        page +
        "&limit=" +
        limt +
        "&sortColumn=" +
        sortColumn +
        "&sortDirection=" +
        SortDirection;

    return this.http.get<any>(url).pipe(map((obj) => obj || []));
  }

  getParentHome() {
    let url = this.baseUrl + "/users/parents/home";
    return this.http.get<any>(url).pipe(map((obj) => obj || []));
  }
  
  getAssessmentDetail(id:String) {
    let url = this.baseUrl + "/assements/"+id;
    return this.http.get<any>(url).pipe(map((obj) => obj || {}));
  }

  getAssessmenttype() {
    let url = this.baseUrl + "/assement-types";
    return this.http.get<any>(url).pipe(map((obj) => obj || {}));
  }

  getAssessmentscore(id:any) {
    let url = this.baseUrl + "/assement-scores/"+id;
    return this.http.get<any>(url).pipe(map((obj) => obj || {}));
  }


  createAssessment(assessment:any){
    let url = this.baseUrl + "/assements";

    return this.http.post<any>(url,assessment).pipe(map((obj) => obj || []));
  }

  updateAssessment(assessment:any){
    let url = this.baseUrl + "/assements/"+assessment.assessmentId;

    return this.http.put<any>(url,assessment).pipe(map((obj) => obj || []));
  }
  onCreateScore(assessmentScore:any) {
    let url = this.baseUrl + "/assement-scores"
    var formData: any = new FormData();
    // formData.append("title", assessmentScore.title);
    formData.append("classId", assessmentScore.classId);
    formData.append("assement", assessmentScore.assementId);
    formData.append("concept", assessmentScore.concept);
    formData.append("maxScore", assessmentScore.maxScore);
    formData.append("assementType", assessmentScore.assementType);
    formData.append("file", assessmentScore.file);
    
    return this.http.post(url, formData);
  }

  onUpdateScore(assessmentScore:any) {
    let url = this.baseUrl + "/assement-scores/"+assessmentScore.id;
    var formData: any = new FormData();
    // formData.append("title", assessmentScore.title);
    formData.append("classId", assessmentScore.classId);
    formData.append("assement", assessmentScore.assementId);
    formData.append("concept", assessmentScore.concept);
    formData.append("maxScore", assessmentScore.maxScore);
    formData.append("assementType", assessmentScore.assementType);
    formData.append("file", assessmentScore.file);
    
    return this.http.put(url, formData);
  }

}
