import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';

import { environment } from "@env/environment";
import { LessonPlan } from '@model/lessonPlan';

@Injectable({
  providedIn: 'root'
})
export class LessonplanService {

  private baseUrl = environment.apiUrl;

  constructor(private http: HttpClient) { }

  getLessonList(searchData: String, page: Number, limt: Number, sortColumn: String, SortDirection: String) {
    let url: any;
    if (searchData) url = this.baseUrl + "/lesson-plans" + "?search=" + searchData;
    else
      url =
        this.baseUrl +
        "/lesson-plans" + "?page=" +
        page +
        "&limit=" +
        limt +
        "&sortColumn=" +
        sortColumn +
        "&sortDirection=" +
        SortDirection;

    return this.http.get<any>(url).pipe(map((obj) => obj || []));
  }

  onCreateLessonPlan(lessonPlan: LessonPlan) {
    let url = this.baseUrl + "/lesson-plans/"
    var formData: any = new FormData();
    formData.append("name", lessonPlan.name);
    formData.append("lesssonPlan", lessonPlan.lesssonPlan);

    return this.http.post(url, formData);
  }

  onUpdateLessonPlan(lessonPlan: LessonPlan) {
    let url = this.baseUrl + "/lesson-plans/"+lessonPlan._id;
    var formData: any = new FormData();
    formData.append("name", lessonPlan.name);
    formData.append("lesssonPlan", lessonPlan.lesssonPlan);

    return this.http.put(url, formData);
  }

  getLessonPlanDetail(lessonPlanId:String) {
    let url = this.baseUrl + "/lesson-plans/"+lessonPlanId;
    return this.http.get<any>(url).pipe(map((obj) => obj || {}));
  }
}
