import { Injectable } from '@angular/core';
import { AdminSideMenu, UserExpands, CommonExpands, TeacherSideMenu, } from "@enum/app_enum";
import { Location } from "@angular/common";

import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';

import { environment } from "@env/environment";
@Injectable({
  providedIn: 'root'
})
export class SupportService {
  private baseUrl = environment.apiUrl;

  constructor(private _loc: Location,private http: HttpClient) { }

  public getIcon(type: String, status = null) {
    let url = "assets/icons/";
    switch (type) {
      case AdminSideMenu.Home:
        return `${url + this.checkIconType("home", status) + ".svg"}`;
      case AdminSideMenu.User:
        return `${url + this.checkIconType("users", status) + ".svg"}`;
      case AdminSideMenu.Class:
        return `${url + this.checkIconType("class", status) + ".svg"}`;
      case AdminSideMenu.Year:
        return `${url + this.checkIconType("year", status) + ".svg"}`;
      case AdminSideMenu.Report:
        return `${url + this.checkIconType("report", status) + ".svg"}`;
      case TeacherSideMenu.LessonPlan:
        return `${url + this.checkIconType("lessonPlan", status) + ".svg"}`;
      case CommonExpands.Manage:
        return `${url + this.checkIconType("management", status) + ".svg"}`;
      case CommonExpands.Create:
        return `${url + this.checkIconType("create", status) + ".svg"}`;
      case UserExpands.Admin:
      case UserExpands.Teacher:
      case UserExpands.StudentAffair:
      case UserExpands.Parent:
      case UserExpands.Student:
        return `${url + this.checkIconType("user", status) + ".svg"}`;
      default:
        return null;
    }
  }

  checkIconType(name:String, status:String) {
    return status ? name + "-" + status : name;
  }

  goBackPrev() {
    this._loc.back();
  }

  onDelete(id:String,type:String){
    console.log(id,"<<>>",type);
    let url=this.baseUrl + "/"+type+"/"+id;

    return this.http
      .delete<any>(url)
      .pipe(map((obj) => obj || {}));
  }
}
