import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { environment } from "@env/environment";

@Injectable({
  providedIn: 'root'
})
export class YearService {
  private baseUrl = environment.apiUrl;

  constructor(private http: HttpClient) { }

  getYearList() {
    let url = this.baseUrl + "/years"
    return this.http.get<any>(url).pipe(map((obj) => obj || []));
  }

  onCreateYear(year:String){
    let url = this.baseUrl + "/years";
    let data={
      name:year
    };
    return this.http.post<any>(url,data).pipe(map((obj) => obj || []));
  }

  onEditYear(year:String,id:String){
    let url = this.baseUrl + "/years/"+id;
    let data={
      name:year
    };
    return this.http.put<any>(url,data).pipe(map((obj) => obj || []));
  }

  onDeleteYear(id:String){
    let url=this.baseUrl + "/years/"+id;

    return this.http
      .delete<any>(url)
      .pipe(map((obj) => obj || {}));
  }

}
