import { Component, OnInit } from '@angular/core';
import {
  AdminSideMenu, UserExpands, CommonExpands, TeacherSideMenu,
  AdminRoute, StudentAffairRoute, TeacherRoute, ParentRoute, StudentRoute, IconStatus
} from "@enum/app_enum";
import { Router, ActivatedRoute } from '@angular/router';
import { AuthenticationService } from "@core/authentication/authentication.service";
import { SupportService } from "@core/services/serviceIndex";
@Component({
  selector: 'app-side-menu',
  templateUrl: './side-menu.component.html',
  styleUrls: ['./side-menu.component.css']
})
export class SideMenuComponent implements OnInit {
  routeIndex = 0;
  public isImgLoad: boolean = true;
  public currentRoutes: any;
  menuLists = {
    admin:
      [
        { id: 1, name: AdminSideMenu.Home, expand: [], routeName: AdminRoute.Home, activeRoute: 'home', },
        {
          id: 2, name: AdminSideMenu.User, routeName: AdminRoute.UserAdmin, activeRoute: 'users',
          expand: [
            { id: 1, name: UserExpands.Admin, routeName: AdminRoute.UserAdmin, expandRoute: 'admin-management' },
            { id: 2, name: UserExpands.Teacher, routeName: AdminRoute.UserTeacher, expandRoute: 'teacher-management' },
            { id: 3, name: UserExpands.Parent, routeName: AdminRoute.UserParent, expandRoute: 'parent-management' },
            { id: 4, name: UserExpands.Student, routeName: AdminRoute.UserStudent, expandRoute: 'student-management' },
            { id: 5, name: UserExpands.StudentAffair, routeName: AdminRoute.UserStudentAffair, expandRoute: 'studentAffair-management' },
            { id: 6, name: UserExpands.Create, routeName: AdminRoute.UserCreate, expandRoute: 'create' }
          ]
        },
        {
          id: 3, name: AdminSideMenu.Class, routeName: AdminRoute.ClassManagement, activeRoute: 'classes',
          expand: [
            { id: 1, name: CommonExpands.Manage, routeName: AdminRoute.ClassManagement, expandRoute: 'management' },
            { id: 2, name: CommonExpands.Create, routeName: AdminRoute.ClassCreate, expandRoute: 'create' },
          ]
        },
        { id: 4, name: AdminSideMenu.Year, expand: [], routeName: AdminRoute.Year, activeRoute: 'years' },
        { id: 5, name: AdminSideMenu.Report, expand: [], routeName: AdminRoute.Report, activeRoute: 'reports' },
      ],
    studentaffair:
      [
        { id: 1, name: AdminSideMenu.Home, expand: [], routeName: StudentAffairRoute.Home, isExpand: false },
        {
          id: 2, name: AdminSideMenu.User, routeName: StudentAffairRoute.Home,
          expand: [
            { id: 1, name: UserExpands.Admin, routeName: StudentAffairRoute.UserAdmin },
            { id: 2, name: UserExpands.Teacher, routeName: StudentAffairRoute.UserTeacher },
            { id: 3, name: UserExpands.Parent, routeName: StudentAffairRoute.UserParent },
            { id: 4, name: UserExpands.Student, routeName: StudentAffairRoute.UserStudent },
            { id: 5, name: UserExpands.StudentAffair, routeName: StudentAffairRoute.UserStudentAffair }
          ]
        },
        {
          id: 3, name: AdminSideMenu.Class, routeName: StudentAffairRoute.ClassManagement,
          expand: [
            { id: 1, name: CommonExpands.Manage, routeName: StudentAffairRoute.ClassManagement },
            { id: 2, name: CommonExpands.Create, routeName: StudentAffairRoute.ClassCreate },
          ]
        },
        { id: 4, name: AdminSideMenu.Year, expand: [], routeName: StudentAffairRoute.Year },
        { id: 5, name: AdminSideMenu.Report, expand: [], routeName: StudentAffairRoute.Report },
      ],
    teacher:
      [
        { id: 1, name: TeacherSideMenu.Home, expand: [], routeName: TeacherRoute.Home, activeRoute: 'home', },
        {
          id: 2, name: TeacherSideMenu.Class, routeName: TeacherRoute.ClassManagement, activeRoute: 'classes',
          expand: []
        },
        {
          id: 4, name: TeacherSideMenu.LessonPlan,
          routeName: TeacherRoute.LessonPlanManage,
          activeRoute: 'lesson-plans',
          expand: [
            { id: 1, name: CommonExpands.Manage, routeName: TeacherRoute.LessonPlanManage, expandRoute: 'management' },
            { id: 2, name: CommonExpands.Create, routeName: TeacherRoute.LessonCreate, expandRoute: 'create' },
          ],

        },
        { id: 5, name: TeacherSideMenu.Report, expand: [], routeName: TeacherRoute.Report, activeRoute: 'reports' },
      ],
    parent:
      [
        { id: 1, name: TeacherSideMenu.Home, expand: [], routeName: ParentRoute.Home, activeRoute: 'home' },
        {
          id: 2, name: TeacherSideMenu.Class, routeName: ParentRoute.ClassManagement, activeRoute: 'classes',
          expand: []
        },
      ],
    student:
      [
        { id: 1, name: TeacherSideMenu.Home, expand: [], routeName: StudentRoute.Home, activeRoute: 'classes'  },
      ],
  };

  public tempmenu: any;
  public currentMenus: any = [];
  public currentRoute: any;
  public userData: any = {};

  constructor(private _router: Router, private _auth: AuthenticationService, private _service: SupportService,
    private _activeRoute: ActivatedRoute) { }

  ngOnInit(): void {
    this._activeRoute.params.subscribe(params => {
      console.log("here")
      setTimeout(() => {
        this.currentRoute = this._router.url;
        this.currentRoutes = this._router.url.split("/");
        this.userData = this._auth.currentUserValue;
        console.log(this.userData)
        let role = this.userData.role.replace(/[\s\t]+/g, '');
        this.tempmenu = this.menuLists[role.toLowerCase()];
        this.checkLists();
      }, 200);
    })

  }

  private checkLists() {
    this.tempmenu.map((menu: any, index: any) => {
      if (this.currentRoute == menu.routeName)
        this.routeIndex = index;
      let tempMenu = {
        id: menu.id,
        label: menu.name,
        icon: this._service.getIcon(menu.name),
        expand: [],
        routeName: menu.routeName,
        isExpand: this.currentRoutes.includes(menu.activeRoute)
      };
      menu.expand.map((expand: any) => {
        tempMenu.expand.push(
          {
            id: expand.id,
            label: expand.name,
            icon: this._service.getIcon(expand.name),
            activeIcon: this._service.getIcon(expand.name, IconStatus.active),
            routeName: expand.routeName,
            expandRoute: expand.expandRoute
          }
        );
      });
      this.currentMenus.push(tempMenu);
    });
  }


  goToroute(name: any, index: any) {
    this._router.navigateByUrl(name);
    setTimeout(() => {
      this.currentMenus[this.routeIndex].isExpand = false;
      this.routeIndex = index;
      this.currentRoute = this._router.url;
      this.currentMenus[index].isExpand = true;
      this.currentRoutes = this._router.url.split("/");
    }, 200);

  }

  logout() {
    this._auth.logout();
    this._router.navigateByUrl("login");
  }

  onImgLoad() {
    this.isImgLoad = false;
}

}
