import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { environment } from "@env/environment";
import { Report } from '@app/shared/models/report';

@Injectable({
  providedIn: 'root'
})
export class ReportService {

  private baseUrl = environment.apiUrl;

  constructor(private http: HttpClient) { }

  getUsersClasses(page: any, limit: any, direction: String, search:String) {
    let url: any;
    if (search)
      url = this.baseUrl + "/classes?search=" + search;
    else if (direction)
      url = this.baseUrl + "/classes?direction=" + direction + "&page=" + page + "&limit=" + limit;
    else url = this.baseUrl + "/classes?page=" + page + "&page=" + page;

    return this.http.get<any>(url).pipe(map((obj) => obj || []));
  }

  getReportStudents(searchData: String, page: Number, limt: Number, sortColumn: String, SortDirection: String,studentId:String) {
    let url: any;
    if (searchData) url = this.baseUrl + "/reports" + "?search=" + searchData +"&studentId="+studentId;
    else
      url =
        this.baseUrl +
        "/reports" + "?page=" +
        page +
        "&limit=" +
        limt +
        "&sortColumn=" +
        sortColumn +
        "&sortDirection=" +
        SortDirection
        +"&studentId="+studentId;

    return this.http.get<any>(url).pipe(map((obj) => obj || []));
  }

  onCreateReport(report: Report) {
    let url = this.baseUrl + "/reports"
    var formData: any = new FormData();
    formData.append("note", report.note);
    formData.append("report", report.report);
    formData.append("studentId", report.studentId);
    
    return this.http.post(url, formData);
  }

  onEditReport(report: Report) {
    let url = this.baseUrl + "/reports/"+report._id;
    var formData: any = new FormData();
    formData.append("note", report.note);
    formData.append("report", report.report);
    formData.append("studentId", report.studentId);
    
    return this.http.put(url, formData);
  }

  getReportDetail(reportId:String) {
    let url = this.baseUrl + "/reports/"+reportId;
    return this.http.get<any>(url).pipe(map((obj) => obj || {}));
  }

}
