import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AuthGuardService } from "@core/guards/auth-guard.service";
import { SideMenuComponent } from "@modules/side-menu/side-menu.component";
import { Role } from "@enum/app_enum";

const routes: Routes = [
  {
    path: 'login',
    loadChildren: () => import('@modules/login/login.module')
      .then(m => m.LoginModule),
  },
  {
    path: 'admin',
    component: SideMenuComponent,
    canActivate: [AuthGuardService],
    children: [
      {
        path: 'home',
        loadChildren: () => import('@modules/home/home.module')
          .then(m => m.HomeModule),
      },
      {
        path: 'users',
        loadChildren: () => import('@modules/users/users.module')
          .then(m => m.UsersModule),
          data: { roles: [Role.Admin] }
      },
      {
        path: 'classes',
        loadChildren: () => import('@modules/class/class.module')
          .then(m => m.ClassModule),
      },
      {
        path: 'years',
        loadChildren: () => import('@modules/year/year.module')
          .then(m => m.YearModule),
      },
      {
        path: 'reports',
        loadChildren: () => import('@modules/report/report.module')
          .then(m => m.ReportModule),
      },
    ]
  },
  {
    path: 'teacher',
    component: SideMenuComponent,
    canActivate: [AuthGuardService],
    children: [
      {
        path: 'home',
        loadChildren: () => import('@modules/home/home.module')
          .then(m => m.HomeModule),
      },
      {
        path: 'classes',
        loadChildren: () => import('@modules/class/class.module')
          .then(m => m.ClassModule),
      },
      {
        path: 'lesson-plans',
        loadChildren: () => import('@modules/lessonplan/lessonplan.module')
          .then(m => m.LessonplanModule),
      },
      {
        path: 'reports',
        loadChildren: () => import('@modules/report/report.module')
          .then(m => m.ReportModule),
      },
    ]
  },
  {
    path: 'student affair',
    component: SideMenuComponent,
    canActivate: [AuthGuardService],
    children: [
      {
        path: 'home',
        loadChildren: () => import('@modules/home/home.module')
          .then(m => m.HomeModule),
      },
      {
        path: 'users',
        loadChildren: () => import('@modules/users/users.module')
          .then(m => m.UsersModule),
      },
      {
        path: 'classes',
        loadChildren: () => import('@modules/class/class.module')
          .then(m => m.ClassModule),
      },
      {
        path: 'years',
        loadChildren: () => import('@modules/year/year.module')
          .then(m => m.YearModule),
      },
      {
        path: 'reports',
        loadChildren: () => import('@modules/report/report.module')
          .then(m => m.ReportModule),
      },
    ]
  },
  {
    path: 'parent',
    component: SideMenuComponent,
    canActivate: [AuthGuardService],
    children: [
      {
        path: 'home',
        loadChildren: () => import('@modules/home/home.module')
          .then(m => m.HomeModule),
      },
      {
        path: 'classes',
        loadChildren: () => import('@modules/class/class.module')
          .then(m => m.ClassModule),
      },
    ]
  },
  {
    path: 'student',
    component: SideMenuComponent,
    canActivate: [AuthGuardService],
    children: [
      {
        path: 'classes',
        loadChildren: () => import('@modules/class/class.module')
          .then(m => m.ClassModule),
      },
    ]
  },
  { path: '', redirectTo: '/login', pathMatch: 'full' },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
